import React from 'react';
import PropTypes from 'prop-types';

function Events__Checkbox(props) {
    const stringLength = 16,
        stringOffset = -4,
        randomNumber = Math.random().toString(stringLength).slice(stringOffset),
        url =
            props.responseParameter &&
            props.responseParameter.REQUEST_URL &&
            new URL(props.responseParameter.REQUEST_URL),
        params =
            props.responseParameter &&
            props.responseParameter.REQUEST_URL &&
            new URLSearchParams(url.search.slice(1)),
        toggleChecked =
            params && params.get('events') === 'past' ? true : false;

    const toggleFacet = () => {
        const newQuery = props.query;

        toggleChecked ? (newQuery.events = '') : (newQuery.events = 'past');

        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    return (
        <div className="finder__filter finder__checkbox">
            <input
                type="checkbox"
                id={`past_events--${randomNumber}`}
                name="events"
                value="past"
                onChange={() => toggleFacet()}
            />
            <div
                className="finder__checkbox__indicator finder__checkbox__indicator"
                aria-hidden="true"
                onClick={() => toggleFacet()}
            >
                {toggleChecked ? (
                    <span className="fa fa-fw fas fa-check icon"></span>
                ) : null}
            </div>
            <label
                className="finder__filters__label--always"
                htmlFor={`past_events--${randomNumber}`}
            >
                Past events
            </label>
        </div>
    );
}

Events__Checkbox.propTypes = {
    facet: PropTypes.object,
    responseParameter: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
    dependencies: PropTypes.arrayOf(PropTypes.object),
};

export default Events__Checkbox;
